* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  float: left;
  margin: auto;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.nav__bar ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  font-size: 20px; 
  width: 50%;
  
}

.nav__bar a {
  text-decoration: none;
  color:black;
  padding:10px;
}

.nav__bar li {
  list-style: none;
  margin: 18px;
}

.nav__bar a:hover {
  background-color: black;
  color: red;
  border-radius: 10px;
  opacity: 0.95;
}

.card img {
 width: 300px;

}

.card img:hover {
  opacity: 0.5;
}
.card {
  width: 300px;
  display: flex;
  flex-direction: column; 
  margin: auto;
  align-items: center;
  justify-content: space-around;
  border-radius: 50px;
}
.card h3 {
  font-size: 20px;
  margin: 20px;
}
.card p {
  font-size: 14px;
}

.card a {
  text-decoration: none;
  color: black;
  padding: 10px;
  margin: 10px;
}
 .btn {
  color: rgb(255, 255, 255) !important;
  background: black;
 }

 .btn:hover {
   opacity: 0.5;
   color: rgb(255, 0, 0) !important;
 }

.more {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
grid-template-rows: repeat(minmax, 1fr);
grid-column-gap: 0px;
grid-row-gap: 0px;

}

.lines {
  width: 50px;
  height: 50px;
  background: black;
}

